@import './variable.scss';
.section{
  padding-top: 8rem;
}
.fp-watermark{
  display: none;
}
#header{
  top:0px;
}
.section{
  background-color: #f9f9f9;
}
#fp-nav.fp-right{
  width: 33rem;
  top: 6rem;
  height: 8rem;
  ul{
    font-size: 1.7rem;
    font-weight: 600;
    display: flex;
    gap: 50px;
    li:nth-child(1){
      @extend  %li; 
    }
     li:nth-child(2) {
        @extend  %li; 
        a::before{
          content: "About"
        }
    }
    li:nth-child(3){
      @extend  %li; 
      a::before{
        content: "Project"
      }
    }
    li:nth-child(4) {
      @extend  %li; 
      a::before{
        content: "Contact"
      }
  }
    
  }
}
  




